import request from "@/apis/http";
import { AxiosPromise } from "axios";
import {
  CompaniesParams,
  CompanyCardResponse,
  CompanyResponse
} from "@/typings/company";
import { MemberTeam } from "@/typings/member";

export function getCompanies(
  data: CompaniesParams
): AxiosPromise<CompanyCardResponse[]> {
  return request({
    url: "/companies/",
    method: "GET",
    params: data
  });
}

export function getCompany(id: string): AxiosPromise<CompanyResponse> {
  return request({
    url: `/company/${id}/`,
    method: "GET"
  });
}

export function getCompanyTeams(id?: number): AxiosPromise<MemberTeam[]> {
  const url = id ? `/company/${id}/teams/` : `/company/teams/`;
  return request({
    url,
    method: "GET"
  });
}
